/* Footer */
footer {
  padding: 100px 20px;
  background-color: $brandColor;
  .footer-alt {
    ul {
      li {
        a {
          border-radius: 50%;
          transition: all 0.5s;
          background-color: rgba(241, 241, 241, 0.08);
          width: 40px;
          height: 40px;
          display: block;
          font-size: 16px;
          line-height: 42px;
          text-align: center;
          &:hover {
            background-color: rgba(241, 241, 241, 0.04);
          }
        }
      }
    }
  }
}