body { font-family: 'Montserrat', sans-serif }
h2 {
  font-size: 32px;
  line-height: 38.5px;
  color: $brandColor;
  margin-bottom: 20px;
  @media(max-width: 575.98px) {
    font-size: 24px;
    line-height: 30.5px;
    margin-bottom: 10px;
  }
  @media (min-width: 576px) and (max-width: 767.98px) {
    font-size: 26px;
    line-height: 32.5px;
    margin-bottom: 12px;
  }
  @media (min-width: 768px) and (max-width: 991.98px) {
    font-size: 28px;
    line-height: 34.5px;
    margin-bottom: 14px;
  }
  span { font-weight: 600 }
}
.text {
  margin: auto;
  font-size: 16px;
  line-height: 24px;
  color: $primaryColor;
  max-width: 600px;
  @media(max-width: 575.98px) {
    font-size: 14px;
  }
  @media (min-width: 576px) and (max-width: 767.98px) {
    font-size: 14px;
  }
  @media (min-width: 768px) and (max-width: 991.98px) {
    font-size: 15px;
  } }
p { font-family: 'Nunito', sans-serif;
  @media(max-width: 575.98px) {
    font-size: 14px !important;
  }
  @media (min-width: 576px) and (max-width: 767.98px) {
    font-size: 14px !important;
  }
  @media (min-width: 768px) and (max-width: 991.98px) {
    font-size: 15px !important;
  }
}