/* Navigation Bar */
.navbar {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 80px;
	z-index: 999;
	transition: all .3s linear;
	@media(max-width: 575.98px) {
		background-color: #fff;
		height: auto;
  }
  @media (min-width: 576px) and (max-width: 767.98px) {
    background-color: #fff;
		height: auto;
  }
  @media (min-width: 768px) and (max-width: 991.98px) {
    background-color: #fff;
		height: auto;
  }
	.navbar-brand {
		color: #fff;
		font-size: 18px;
		font-weight: 400;
    text-shadow: 0 0 1px rgba(#fff, .3);
    @media(max-width: 575.98px) {
      color: #000;
    }
    @media (min-width: 576px) and (max-width: 767.98px) {
      color: #000;
    }
    @media (min-width: 768px) and (max-width: 991.98px) {
      color: #000;
    }
  }
	.navbar-collapse {
		.navbar-nav {
			.nav-item {
				.nav-link {
					color: #fff;
					font-size: 15px;
					font-weight: 300;
					padding: 0 15px;
					transition: .3s;
          text-shadow: 0 0 1px rgba(#fff, .3);
          @media(max-width: 575.98px) {
            color: #000;
            padding: 5px;
          }
          @media (min-width: 576px) and (max-width: 767.98px) {
            color: #000;
            padding: 5px;
          }
          @media (min-width: 768px) and (max-width: 991.98px) {
            color: #000;
            padding: 5px;
          }
					&:hover {
						color: $mainColor;
					}
				}
			}
			.nav-item.active {
				.nav-link {
					color: $mainColor !important; }
			}
		}
	}
}
.show {
	top: 0;
	left: 0;
	width: 100%;
	height: 60px;
	background-color: #fff !important;
	box-shadow: 0 0 5px 0px rgba(#000, .3);
  z-index: 999;
  @media(max-width: 575.98px) {
    height: auto;
    box-shadow:none;
  }
  @media (min-width: 576px) and (max-width: 767.98px) {
    height: auto;
    box-shadow:none;
  }
  @media (min-width: 768px) and (max-width: 991.98px) {
    height: auto;
    box-shadow:none;
  }
	.navbar-brand {
		color: #000;
	}
	.navbar-collapse {
		.navbar-nav {
			.nav-item {
				.nav-link {
					color: #000;
					&:hover {
						color: $mainColor;
					}
				}
			}
			.nav-item.active {
				.nav-link {
					color: $mainColor !important; }
			}
		}
	}
}