/* Header */
header {
  background-image: url("https://images.unsplash.com/photo-1517694712202-14dd9538aa97?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1500&q=80");
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.69);
  }
  .content {
    margin: 0 auto;
    position: relative;
    z-index: 9;
    color: #fff;
    text-align: center;
    max-width: 800px;
    > span {
      display: block;
      font-size: 24px;
      font-weight: 300;
      line-height: 28.8px;
      letter-spacing: 2px;
      text-transform: uppercase;
      @media(max-width: 575.98px) {
        font-size: 18px;
      }
      @media (min-width: 576px) and (max-width: 767.98px) {
        font-size: 19px;
      }
      @media (min-width: 768px) and (max-width: 991.98px) {
        font-size: 20px;
      }
    }
    h2 {
      color: #fff;
      text-align: center;
      overflow: hidden;
      font-size: 54px;
      line-height: 64.8px;
      font-weight: 600;
      margin: 24px 0;
      height: 60px;
      line-height: 60px;
      overflow: hidden;
      display: inline-block;
      @media(max-width: 575.98px) {
        font-size: 34px;
        margin: 10px 0;
        height: 35px;
        line-height: 35px;
      }
      @media (min-width: 576px) and (max-width: 767.98px) {
        font-size: 38px;
        margin: 12px 0;
        height: 38px;
        line-height: 38px;
      }
      @media (min-width: 768px) and (max-width: 991.98px) {
        font-size: 42px;
        margin: 14px 0;
        height: 38px;
        line-height: 38px;
      }
    }
    p {
      font-size: 16px;
      line-height: 28.8px;
      max-width: 700px;
      margin: 0 auto;
      font-weight: 300;
      color: rgba(255, 255, 255, 0.75);
      @media(max-width: 575.98px) {
        font-size: 14px;
        line-height: 24.8px;
      }
      @media (min-width: 576px) and (max-width: 767.98px) {
        font-size: 14px;
      line-height: 24.8px;
      }
      @media (min-width: 768px) and (max-width: 991.98px) {
        font-size: 15px;
        line-height: 24.8px;
      }
    }
  }
}