/* About Me */
.about-me {
  padding: 80px 20px;
  .img {
    box-shadow: 0 0 10px 0 #dee2e6;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      top: 1%;
      left: 1.5%;
      width: 97.5%;
      height: 98%;
      background-color: rgba(0, 0, 0, 0.28);
    }
  }
  .about-me-content {
    padding-left: 20px;
    @media(max-width: 575.98px) {
      margin-top: 30px;
    }
    @media (min-width: 576px) and (max-width: 767.98px) {
      margin-top: 30px;
    }
    h3 {
      font-size: 24px;
      line-height: 28.8px;
      color: $brandColor;
      margin-bottom: 15px;
      @media(max-width: 575.98px) {
        font-size: 20px;
      }
      @media (min-width: 576px) and (max-width: 767.98px) {
        height: auto;
        box-shadow:none;
      }
      @media (min-width: 768px) and (max-width: 991.98px) {
        height: auto;
        box-shadow:none;
      }
      span {
        color: $mainColor;
        font-weight: 600;
      }
    }
    p {
      font-size: 16px;
      line-height: 24px;
      color: $primaryColor;
      max-width: 600px;
    }
  }
  .about-social {
    padding-left: 20px;
    li {
      a {
        display: block;
        font-size: 17px;
        color: #000;
        height: 40px;
        width: 40px;
        border-radius: 50%;
        border: 2px solid #d4d4d4;
        line-height: 38px;
        text-align: center;
        transition: all .4s;
        &:hover {
          color: $mainColor;
          border-color: $mainColor;
        }
      }
    }
  }
}

/* My Services */
.services {
  padding: 80px 20px;
  background-color: #F8F9FA;
  .services-boxes {
    border: 1px solid #e8e8e8;
    padding: 30px;
    &:hover {
      h5 {
        &::after {
          width: 50px;
        }
      }
    }
    .services-boxes-icon {
      i {
        color: $mainColor;
        font-size: 40px;
      }
    }
    h5 {
      position: relative;
      font-size: 1.05em;
      font-weight: 600;
      &::after {
        content: "";
        display: block;
        margin: 15px auto;
        width: 40px;
        height: 2px;
        border-radius: 50px;
        background-color: $mainColor;
        transition: all .3s ease;
      }
    }
  }
}

/* Section Freelancer */
.freelancer {
  padding: 80px 20px;
  .btn {
    background-color: $mainColor;
    font-size: 15px;
    font-weight: 600;
    padding: 15px 32px;
    color: #fff;
    &:focus {
      box-shadow: 0 0 0 0.2rem rgba($mainColor,.25);
    }
  }
}

/* My Clients */
.my-clients {
  padding: 80px 20px;
  background-color: #F8F9FA;
  .carousel {
    padding: 30px 0;
    .carousel-indicators {
      bottom: -25px;
      li {
        position: relative;
        display: block;
        background-color: $mainColor;
        width: 15px;
        height: 6px;
        border-radius: 50%;
        transition: all .5s;
      }
      li.active { width: 20px }
    }
  }
}

/* Section Works */
.portfolio {
  padding: 80px 20px;
  .work-item {
    position: relative;
    overflow: hidden;
    border-radius: 3px;
    margin-top: 30px;
    border: 1px solid #dee2e6;
    height: 215px;
    &:hover {
      .work-details {
        top: 0;
      }
      img {
        transform: scale(1.2);
      }
    }
    &::after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(#000,.1);
    }
    .work-img {
      img {
        transition: all .4s;
      }
    }
    .work-details {
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(#000, .9);
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: all .5s;
      p {
        font-size: 14px;
        color: #dee2e6;
      }
      h4 {
        text-transform: uppercase;
        font-size: 18px;
        color: #fff;
      }
    }
  }
}

/* Section Contact Me */
.contact-me {
  padding: 80px 20px;
  background-color: #F8F9FA;
  i {
    color: $mainColor;
    font-size: 40px;
  }
  h5 {
    font-size: 18px;
    font-weight: 600;
  }
} 